@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
}

.app-container {
  display: flex;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(135deg, #00195f, #1a0035); /* Blue gradient */
  color: white;
}

.left-panel {
  width: 50%;  /* Left panel takes up 50% */
  height: 100vh;
  background-color: transparent;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;  /* Left-align content */
  padding: 2rem;
  font-family: 'Inter', sans-serif;
}

.right-panel {
  width: 50%;  /* Right panel takes up 50% */
  height: 100vh;
  overflow-y: scroll;
  background-color: transparent;
  padding: 2rem;
  color: white;
}

html {
  scroll-behavior: smooth;
}


/* Example media query for mobile screens */
@media (max-width: 768px) {
  .right-panel, .left-panel {
    width: 100%;
    padding: 1rem;
  }

  .project-card {
    flex-direction: column;
  }

  .project-image {
    width: 100%; /* Full width for mobile */
    margin-bottom: 1rem;
  }

  .avatar-panel {
    height: 250px; /* Smaller height for mobile */
  }

  .right-panel h3, .left-panel h1, .left-panel h2 {
    font-size: 1.2rem;
  }

  .experience-item, .project-card {
    padding: 1rem;
    margin: 1rem 0;
  }
}


