.project-card {
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 1.5rem;
    transition: background-color 0.3s ease, transform 0.2s;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .project-card:hover {
    background-color: rgba(255, 255, 255, 0.1);
    transform: translateY(-5px);
  }
  
  .project-image {
    width: 100px;
    height: 100px;
    overflow: hidden;
    border-radius: 8px;
    margin-right: 1.5rem;
    opacity: 0.7
  }
  
  .project-image:hover {
    opacity: 1; /* Full opacity on hover */
}

  .project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .project-content {
    flex: 1;
  }
  
  .project-header {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .project-title {
    color: #64ffda;
    font-size: 0.97rem;
    font-weight: bold;
  }
  
  .project-description {
    color: #a8b2d1;
    font-size: 0.8rem !important;
    margin: 0.5rem 0;
  }
  
  .project-technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .technology-tag {
    background-color: rgba(255, 255, 255, 0.1);
    color: #cfd8e3;
    font-size: 0.85rem;
    padding: 0.3rem 0.7rem;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .technology-tag:hover {
    background-color: #64ffda;
    color: #0a192f;
  }
  