.right-panel {
    width: 50%;
    height: 100vh;
    overflow-y: scroll;
    background-color: #ffffff;
    padding: 2rem;
    color: rgb(0, 0, 0);
  }


  
  /* Basic styling for each section */
  .right-panel section {
    margin: 2rem 0;
    padding: 1.5rem;
    background: transparent;
    border-radius: 8px;
    transition: transform 0.5s ease, opacity 0.5s ease;
    opacity: 0;
    transform: translateY(50px);

  }
  
  /* Class added when the section is visible */
  .right-panel section.visible {
    opacity: 1;
    transform: translateY(0);
  }
  
  .avatar-section {
    width: 100% !important;
    height: 400px !important; /* Ensure there's enough height for the Spline scene */
    display: flex !important;
    justify-content: center;
    align-items: center;
  }
  .right-panel h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .right-panel p,
  .right-panel ul {
    font-size: 1rem;
    line-height: 1.5;
  }
  
  .right-panel ul {
    list-style-type: none;
    padding: 0;
  }
  
  .right-panel li {
    margin-bottom: 0.5rem;
  }
  
  .about-section {
    padding-top: 4rem !important; /* Adjust this value as needed */
    margin-top: -4rem; /* Negative margin to counteract the padding */
  }

  .about-section p {
    color: #aaa; /* Lighter shade of white */
    margin-bottom: 1rem; /* Adds space between paragraphs */
    line-height: 1.6; /* Better readability */
  }

  /* Styling for bold text within the About section */
.about-section b {
  color: #ffffff; /* Full white color */
  font-weight: bold;
}

  /* Link styling with hover effect */
  .boba-link {
    color: #ffffff; /* Initial link color */
    text-decoration: none;
    transition: color 0.3s ease;
  }

  .boba-link:hover {
    color: #ffcc66; /* Light golden shade on hover */
  }

  #experience {
    margin-top: 2rem;
  }
  
  .experience-item {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 1.5rem;
    transition: background-color 0.3s ease;
  }
  
  .experience-item:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  
  .experience-header {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .experience-time {
    color: #8892b0;
    font-size: 0.75rem;
    font-weight: 500;
    margin-right: 1rem;
  }
  
  .experience-title {
    font-size: 0.97rem;
    font-weight: 600;
    color: #64ffda;
  }
  
  .experience-title span {
    color: #cfd8e3;
    font-weight: 400;
  }
  
  .experience-description {
    font-size: 0.8rem !important;
    line-height: 1.5 !important;
    margin: 0.5rem 0;
    color: #a8b2d1;
  }
  
  .experience-technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .technology-tag {
    background-color: rgba(255, 255, 255, 0.1);
    color: #cfd8e3;
    font-size: 0.85rem;
    padding: 0.3rem 0.7rem;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .technology-tag:hover {
    background-color: #64ffda;
    color: #0a192f;
  }


  .skills-section {
    margin-top: 2rem;
  }
  
  .skills-section h3 {
    text-align: center;
    color: #ffffff;
  }

  .resume-link {
    display: inline-block;
    margin-top: 3rem;
    font-size: 0.9rem;
    color: #b0c4de;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease, transform 0.3s ease;
  }
  
  .resume-link:hover {
    color: #fff;
    transform: translateX(5px); /* Subtle move on hover */
  }

  @media (max-width: 768px) {
    .app-container {
      flex-direction: column;
    }
  }
  